import axios from '../api/axios';

//////////////////////  //////////////////////

//  
export const editUserInfo = async (value) => {
  try {
    const { newPassword, oldPassword, displayName } = value;
    const token = localStorage.getItem('accessToken');
    if (token !== null) {
      axios.defaults.headers.common['authorization'] = `Bearer ${token}`;
      const response = await axios.put(`/auth/user`, {
        displayName,
        oldPassword,
        newPassword,
      });
      // console.log(response);
      return response;
    }
  } catch (err) {
    console.log(err);
  }
};

// 
export const signup = async (value) => {
  //console.log(value);
  const { email, password, displayName } = value;

  try {
    const { data } = await axios.post('/auth/signup', {
      email,
      password,
      displayName,
    });

    localStorage.setItem('accessToken', data.accessToken);
    alert(' !   !');
    window.location.replace('/');
  } catch (err) {
    alert(' .');
    console.log(err);
  }
};

// 
export const signIn = async (value) => {
  try {
    const { email, password } = value;
    const { data } = await axios.post(`/auth/login`, {
      email,
      password,
    });
    const { accessToken } = data;

    localStorage.setItem('accessToken', accessToken);
    return data;
  } catch (err) {
    console.log(err);
  }
};

//   ,  type  
export const auth = async (type = 'me') => {
  // type: me =>  , logout => 
  try {
    const token = localStorage.getItem('accessToken');
    if (token !== null) {
      axios.defaults.headers.common['authorization'] = `Bearer ${token}`;
      const { data } = await axios.post(`/auth/${type}`);
      //console.log(data);
      return data;
    }
    return false;
  } catch (err) {
    console.log(err);
  }
};

