//  
export const addProduct = async (key, list) => {
  try {
    list.price = parseInt(list.price, 10);
    if (key) {
      axios.defaults.headers.common['masterKey'] = true;
      const { data } = await axios.post(`/products`, JSON.stringify(list));
      return data;
    }
    return false;
  } catch (err) {
    console.log(err);
  }
  return true;
};

