import Header from "../components/Header";
import Footer from "../components/Footer";
import Product from "../components/Product";
import { auth } from "../utils/useAPI";
import { getAllProduct } from "../utils/useAPI";

import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

import { Swiper, SwiperSlide } from "swiper/react"; // basic
import { Navigation, Pagination, A11y, Autoplay, EffectFade } from "swiper";
import "swiper/css/bundle";

const MainPage = ({ products, setProducts }) => {
  const [user, setUser] = useState(false);

  const [gucci, setGucci] = useState([]);
  const [bottega, setBottega] = useState([]);
  const [louis, setLouis] = useState([]);
  const [chanel, setChanel] = useState([]);
  const [allItems, setAllItems] = useState([]);
  const brand = [gucci, louis, bottega, chanel];
  const sliceBrand = []; //  10    .

  useEffect(() => {
    const authUser = async () => {
      const userInfo = await auth();
      const products = await getAllProduct(true);
      setUser(userInfo);

      setGucci(products.filter((item) => item.tags[0] === "GUCCI"));
      setBottega(products.filter((item) => item.tags[0].includes("BOTTEGA")));
      setLouis(products.filter((item) => item.tags[0].includes("LOUIS")));
      setChanel(products.filter((item) => item.tags[0].includes("CHANEL")));

      setProducts(products.filter((item) => item.tags[0] === "GUCCI"));
      setAllItems(products.slice(0, 10));
    };
    authUser();
  }, [setProducts]);

  const chunk = (products) => {
    for (let i = 0; i < products.length; i += 10) {
      sliceBrand.push(products.slice(i, i + 10));
    }

    return;
  };
  chunk(products);

  return (
    <>
      <Header user={user} />
      <Container>
        <BannerWrap>
          <Swiper
            modules={[Pagination, A11y, Autoplay]}
            spaceBetween={10}
            slidesPerView={2}
            pagination={{ clickable: true }}
            centeredSlides={true}
            loop={true}
            autoplay={{
              delay: 5000,
              disableOnInteraction: false,
            }}
            speed={1500}
            loopFillGroupWithBlank={true}
            touchRatio={0}
            onSlideChange={(swiper) => {
              let item = document.querySelector(".items-swiper");
              if (item) item.classList.remove("fade");

              if (swiper.activeIndex - 2 < 0) setProducts(brand[3]);
              else if (swiper.activeIndex - 2 === 4) setProducts(brand[0]);
              else setProducts(brand[swiper.activeIndex - 2]);

              if (item)
                setTimeout(() => {
                  item.classList.add("fade");
                }, 10);
            }}
          >
            <SwiperSlide>
              <Banner
                style={{ background: 'url("/images/banner/gucci_banner.jpg") center center / cover no-repeat' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <Banner
                style={{ background: 'url("/images/banner/louis_banner.jpg") center center / cover no-repeat' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <Banner
                style={{ background: 'url("images/banner/bottega_banner.jpg") center center / cover no-repeat' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <Banner
                style={{ background: 'url("images/banner/chanel_banner.jpg") center center / cover no-repeat' }}
              />
            </SwiperSlide>
          </Swiper>
        </BannerWrap>

        <ItemsContainer className="items-wrap">
          <Swiper
            modules={[Navigation, A11y]}
            slidesPerView={1}
            speed={700}
          >
            {sliceBrand.map((items, index) => {
              return (
                <SwiperSlide key={index}>
                  <ul className="items-swiper fade">
                    {items.map((item) => {
                      return <Product key={item.id} id={item.id} product={item} />;
                    })}
                  </ul>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </ItemsContainer>

        <ItemsContainer>
          <ItemsSwiperTitle>New Products</ItemsSwiperTitle>
          <Swiper
            modules={[A11y, Autoplay]}
            spaceBetween={30}
            slidesPerView={5}
            speed={1000}
            loop={true}
            loopFillGroupWithBlank={true}
            autoplay={{
              delay: 1000,
            }}
          >
            {allItems.map((item) => {
              return (
                <SwiperSlide className="new-product-wrap" key={item.id}>
                  <div>
                    <Link
                      className="item-image"
                      to={`/product/${item.id}`}
                      style={{ backgroundImage: `url(${item.thumbnail})` }}
                    />
                    <div className="item-info-wrap">
                      <p className="item-brand">[{item.tags[0]}]</p>
                      <p className="item-title">{item.title}</p>
                      <p className="item-price">{`$${item.price.toLocaleString()}`}</p>
                    </div>
                  </div>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </ItemsContainer>

        <BrandInfo>
          <Swiper
            direction={"vertical"}
            modules={[Pagination, Navigation, Autoplay, EffectFade]}
            slidesPerView={1}
            pagination={{
              clickable: true,
            }}
            autoplay={{
              delay: 3000,
              disableOnInteraction: false,
            }}
            touchRatio={0}
            speed={1500}
            loopFillGroupWithBlank={true}
            loop={true}
            effect={"fade"}
            style={{ height: "500px" }}
          >
            <SwiperSlide>
              <div
                className="info-wrap"
                style={{ background: 'url("/images/info_bg/gucci_info_bg.jpg") center center / cover no-repeat' }}
              >
                <p>
                            . 
                   21   ,          
                   .         
                        .
                  <br />
                  <br />
                      .     ,  ,   
                       .
                </p>
                <Link to="/category/all/GUCCI"> </Link>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="info-wrap"
                style={{ background: 'url("/images/info_bg/louis_info_bg.jpg") center center / cover no-repeat' }}
              >
                <p>
                            .    
                      . 1993  '   '
                  , 2005 2 '  '      
                     '   ', '   '  .
                            , 
                     ,  ,        
                    100    '  ',     
                   .
                  <br />
                  <br />            
                        .
                </p>
                <Link to="/category/all/LOUIS"> </Link>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="info-wrap"
                style={{ background: 'url("/images/info_bg/bottega_info_bg.jpg") center center / cover no-repeat' }}
              >
                <p>
                   (Bottega Veneta)          
                  . 1966     ,   
                  (Montebello Vicentino) 18   ,    
                        .
                  <br />
                  <br />
                  2001,     ,      .
                </p>
                <Link to="/category/all/BOTTEGA"> </Link>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="info-wrap"
                style={{ background: 'url("/images/info_bg/chanel_info_bg.jpg") center center / cover no-repeat' }}
              >
                <p>
                       .      
                         .   
                  ,          ,  
                         ,       
                   .          
                   .          
                     ,       .
                  <br />
                  <br />
                  "       ,     
                  ."
                </p>
                <Link to="/category/all/CHANEL"> </Link>
              </div>
            </SwiperSlide>
          </Swiper>
        </BrandInfo>
      </Container>
      <Footer />
    </>
  );
};

export { MainPage };

const Container = styled.main`
  margin: 0 auto;
  overflow: hidden;

  * {
    text-decoration: none;
    box-sizing: border-box;
  }
`;

const BannerWrap = styled.div`
  position: relative;

  .swiper-slide {
    opacity: 0.5;
    pointer-events:none;
  }

  .swiper-slide-active {
    opacity: 1;
  }

  .swiper {
    overflow: inherit;
    width: 140%;
    left: -20%;
    min-width:1200px;
  }

  .swiper-pagination {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: -80px;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;
    overflow: hidden;

    .swiper-pagination-bullet {
      width: 150px;
      height: 200px;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      border-radius: 0;
      background-color: rgba(0, 0, 0, 0);
    }
    .swiper-pagination-bullet:nth-child(1) {
      background-image: url("/images/logo/gucci_logo.png");
    }
    .swiper-pagination-bullet:nth-child(2) {
      background-image: url("/images/logo/louis_logo.png");
      width: 200px;
    }
    .swiper-pagination-bullet:nth-child(3) {
      background-image: url("/images/logo/bottega_logo.png");
      width: 200px;
    }
    .swiper-pagination-bullet:nth-child(4) {
      background-image: url("/images/logo/chanel_logo.png");
    }
  }
`;

const Banner = styled.div`
  height: 500px;
`;

const ItemsContainer = styled.div`
  width: 1200px;
  margin: 110px auto;
  position: relative;

  @keyframes fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .items-category-link {
    margin: 50px auto;
    text-align: center;
  }

  .items-swiper {
    display: flex;
    width: 100%;
    height: 650px;
    gap: 15px;
    opacity: 0;
    flex-wrap: wrap;
    transition: 2s all;
  }

  .items-swiper.fade {
    animation: fade 1.5s forwards;
  }

  //   
  .new-product-wrap {
    position: relative;

    .item-image {
      display: block;
      width: 100%;
      height: 216px;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &:hover::before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      border-radius: 5px;
      position: absolute;
      top: 0;
      left: 0;
      pointer-events: none;
    }

    .item-info-wrap {
      display: none;
    }

    &:hover .item-info-wrap {
      display: block;
      width: 100%;
      padding: 10px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      color: #fff;
      pointer-events: none;

      .item-brand {
        margin: 10px 0 5px;
      }

      .item-title {
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 1.02rem;
      }

      .item-price {
        font-size: 0.9rem;
      }
    }
  }
`;

const ItemsSwiperTitle = styled.p`
  font-size: 2rem;
  text-align: center;
  position: relative;
  margin: 50px 0 20px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    width: 40%;
    height: 2px;
    background: #ddd;
    z-index: -1;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    width: 40%;
    height: 2px;
    background: #ddd;
  }
`;

